              class="list-group-item list-group-item-action"
              :class="{ active: tempData.className === item.className }"
              aria-current="true"
              @click.prevent="selectClass(item.className)"
            >
              {{ item.className }}
            </a>
          </template>
          <button
            class="btn btn-heightlight w-100 d-flex justify-content-between text-light"
            @click="this.$refs.addClass.showModal()"
          >
            <span></span><i class="bi bi-plus-lg"></i>
          </button>
        </div>
      </div>
      <div class="col-9">
        <div class="card text-center">
          <div class="card-header bg-primary">
            <ul class="nav nav-tabs card-header-tabs">
              <li class="nav-item">
                <button
                  class="nav-link"
                  :class="[this.route === '' ? 'active' : 'text-light']"
                  @click="this.route = ''"
                >
                  <i class="bi bi-people-fill"></i>
                  
                </button>
              </li>
              <li class="nav-item">
                <button
                  class="nav-link"
                  :class="[this.route === '' ? 'active' : 'text-light']"
                  @click="this.route = ''"
                >
