                </button>
              </li>
              <li class="nav-item">
                <button
                  class="nav-link"
                  :class="[this.route === '' ? 'active' : 'text-light']"
                  @click="this.route = ''"
                >
                  <i class="bi bi-trophy"></i>
                  
                </button>
              </li>
              <li class="nav-item ms-auto">
                <button
