import Demo from '@/components/group/DemoModal.vue';
import choseData from '@/assets/jsons/chose.json';
import classData from '@/assets/jsons/class.json';

export default {
  data() {
    return {
      route: '',
      sortable: '',
      choseList: choseData,
      classList: classData,
      tempData: {},
      newList: '',
    };
  },
  components: {
    AddClassModal,
    TopicModal,
    ScoreModal,
    Group,
    Demo,
  },
  methods: {
    selectClass(i) {
      this.classList.forEach((item) => {
        if (item.className === i) {
          this.tempData = item;
        }
      });
    },
  },
  mounted() {
    // eslint-disable-next-line prefer-destructuring
    this.tempData = this.classList[0];
  },
};
</script>
