<!-- eslint-disable max-len -->
<template>
  <div class="card-body overflow-y-scroll" style="height: 65vh">
    <Bar id="my-chart-id" :options="chartOptions" :data="chartData" />
  </div>
</template>

<script>
import choseData from '@/assets/jsons/chose.json';
import classData from '@/assets/jsons/class.json';
// eslint-disable-next-line import/no-extraneous-dependencies
import { Bar } from 'vue-chartjs';
// eslint-disable-next-line import/no-extraneous-dependencies
import {
  Chart as ChartJS,
  Title,
  Tooltip,
  Legend,
  BarElement,
  CategoryScale,
  LinearScale,
} from 'chart.js';

ChartJS.register(
  Title,
  Tooltip,
  Legend,
  BarElement,
  CategoryScale,
  LinearScale,
);

export default {
  data() {
    return {
      route: '',
      sortable: '',
      choseList: choseData,
      classList: classData,
      tempData: {},
      newList: '',
      chartData: {
        labels: ['January', 'February', 'March'],
        datasets: [{ data: [40, 20, 12] }],
      },
      chartOptions: {
        responsive: true,
      },
    };
  },
  components: { Bar },
  methods: {
    selectClass(i) {
      this.classList.forEach((item) => {
        if (item.className === i) {
          this.tempData = item;
        }
      });
    },
  },
  mounted() {
    // eslint-disable-next-line prefer-destructuring
    this.tempData = this.classList[0];
  },
};
</script>
