<template>
  <div class="card-body overflow-y-scroll" style="height: 65vh">
    <div class="avtart">
      <template v-for="(item, key) in tempData.group" :key="key">
        <div class="card mb-3">
          <div class="row g-0">
            <div class="col-md-4">
              <div
                class="d-flex justify-content-center align-items-center h-100"
              >
                <div v-html="item.avatar" class="avtart-block"></div>
              </div>
            </div>
            <div class="col-md-8">
              <div class="card-body">
                <h5 class="card-title">{{ item.id }}</h5>
                <p class="card-text">
                  This is a wider card with supporting text below as a natural
                  lead-in to additional content. This content is a little bit
                  longer.
                </p>
                <p class="card-text">
                  <small class="text-body-secondary"
                    ><button class="btn btn-primary"></button></small
                  >
                </p>
              </div>
            </div>
          </div>
        </div>
      </template>
    </div>
  </div>
  <div class="card-footer">
    <button class="btn btn-outline-primary" @click="addGroup"></button>
  </div>
</template>

<style lang="scss" scoped>
.avtart-block {
  height: 75px;
  width: 75px;
  border-radius: 50%;
}
.avtart {
  width: 100%;
  text-align: center;
}
</style>

<script>
// eslint-disable-next-line import/no-extraneous-dependencies
import multiavatar from '@multiavatar/multiavatar';

export default {
  data() {
    return {
      tempData: {},
    };
  },
  props: ['classData'],
  watch: {
    classData() {
      this.tempData = JSON.parse(JSON.stringify(this.classData));
      this.createMultiavatar();
    },
  },
  methods: {
    createMultiavatar() {
      this.tempData.group.forEach((item) => {
        // eslint-disable-next-line no-param-reassign
        item.avatar = multiavatar(item.id);
      });
    },
    addGroup() {
      const newAvatar = multiavatar('jkdshiwqegf');
      this.tempData.group.push({ avatar: newAvatar });
    },
  },
};
</script>
